import React, { useState, useEffect } from 'react';
import communicationService from '../services/communicationService';
import { BusNotification } from '../types';

const BusNotificationScreen: React.FC = () => {
  const [connectionStatus, setConnectionStatus] = useState({ api: false, websocket: false, mqtt: false });
  const [isBlinking, setIsBlinking] = useState(false);
  const [busId] = useState(() => `BUS-${Math.random().toString(36).substr(2, 9).toUpperCase()}`);
  const [routeId] = useState(() => `ROUTE-${Math.floor(Math.random() * 900) + 100}`);
  const [currentLocation, setCurrentLocation] = useState<{lat: number, lng: number} | null>(null);
  const [lastNotification, setLastNotification] = useState<BusNotification | null>(null);
  const [etaData, setEtaData] = useState<any[]>([]);

  useEffect(() => {
    //   
    const initializeCommunication = async () => {
      try {
        //    (MQTT )
        communicationService.initialize({
          busId: busId,
          routeId: routeId,
          routeName: ` ${routeId}`
        });

        //   
        communicationService.on('connection-status', (status) => {
          console.log('  :', status);
          setConnectionStatus(status);
        });

        //    
        communicationService.on('button-pressed', (data) => {
          console.log('  :', data);
          setLastNotification({
            stopId: data.stopId,
            routeId: data.routeId,
            busId: busId,
            routeName: ` ${data.routeId}`,
            message: ` ${data.stopId} `,
            timestamp: data.timestamp,
            type: 'APPROACHING'
          });
          triggerBlink();
        });

        // MQTT   
        communicationService.on('bus-notification', (notification) => {
          console.log('  :', notification);
          setLastNotification(notification);
          triggerBlink();
        });

        // ETA  
        communicationService.on('eta-update', (data) => {
          console.log('ETA :', data);
          setEtaData(prev => [...prev.slice(-4), data]); //  5 
        });

        console.log('   ');
        
      } catch (error) {
        console.error('   :', error);
      }
    };

    initializeCommunication();

    return () => {
      communicationService.disconnect();
    };
  }, [busId, routeId]);

  useEffect(() => {
    let watchId: number;

    if (connectionStatus.mqtt && navigator.geolocation) {
      // GPS   
      watchId = navigator.geolocation.watchPosition(
        (position) => {
          const location = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
            speed: position.coords.speed || 0,
            heading: position.coords.heading || 0
          };
          
          setCurrentLocation(location);
          
          // MQTT  
          // communicationService     
        },
        (error) => {
          console.error('GPS :', error);
        },
        {
          enableHighAccuracy: true,
          timeout: 5000,
          maximumAge: 1000
        }
      );
    }

    return () => {
      if (watchId) {
        navigator.geolocation.clearWatch(watchId);
      }
    };
  }, [connectionStatus.mqtt, busId, routeId]);

  const triggerBlink = () => {
    setIsBlinking(true);
    setTimeout(() => {
      setIsBlinking(false);
    }, 3000); // 3 
  };

  return (
    <div style={{
      width: '100vw',
      height: '100vh',
      backgroundColor: isBlinking ? '#ff0000' : '#ffffff',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      transition: isBlinking ? 'background-color 0.5s ease-in-out' : 'none',
      animation: isBlinking ? 'blink 0.5s ease-in-out infinite' : 'none'
    }}>
      <style>{`
        @keyframes blink {
          0%, 100% { background-color: #ff0000; }
          50% { background-color: #ffffff; }
        }
      `}</style>
      
      <div style={{
        background: 'rgba(0, 0, 0, 0.8)',
        color: 'white',
        padding: '30px',
        borderRadius: '15px',
        textAlign: 'center',
        margin: '20px'
      }}>
        <h1 style={{ fontSize: '3rem', marginBottom: '20px' }}>
             
        </h1>
        
        <div style={{ fontSize: '1.5rem', marginBottom: '15px' }}>
          <strong> ID:</strong> {busId}
        </div>
        
        <div style={{ fontSize: '1.5rem', marginBottom: '15px' }}>
          <strong> ID:</strong> {routeId}
        </div>
        
        <div style={{ fontSize: '1.2rem', marginBottom: '15px' }}>
          <strong> :</strong><br/>
          API: {connectionStatus.api ? '' : ''} |
          WebSocket: {connectionStatus.websocket ? '' : ''} |
          MQTT: {connectionStatus.mqtt ? '' : ''}
        </div>
        
        <div style={{ fontSize: '1.5rem', marginBottom: '15px' }}>
          <strong>GPS :</strong> {currentLocation ? 
            `${currentLocation.lat.toFixed(6)}, ${currentLocation.lng.toFixed(6)}` : 
            '  ...'
          }
        </div>
        
        {etaData.length > 0 && (
          <div style={{ 
            fontSize: '1.2rem', 
            marginTop: '15px',
            padding: '15px',
            backgroundColor: '#2196F3',
            borderRadius: '10px'
          }}>
            <strong>ETA :</strong><br/>
            {etaData.slice(-3).map((eta, index) => (
              <div key={index} style={{ margin: '5px 0' }}>
                 {eta.stopId}: {eta.estimatedArrival}
              </div>
            ))}
          </div>
        )}

        {lastNotification && (
          <div style={{ 
            fontSize: '1.5rem', 
            marginTop: '20px',
            padding: '15px',
            backgroundColor: '#4CAF50',
            borderRadius: '10px'
          }}>
            <strong> :</strong><br/>
             {lastNotification.stopId}  <br/>
            <small>{new Date(lastNotification.timestamp).toLocaleTimeString()}</small>
          </div>
        )}

        <div style={{ marginTop: '20px' }}>
          <button 
            onClick={() => {
              //   
              const status = communicationService.getConnectionStatus();
              console.log('  :', status);
            }}
            style={{
              padding: '10px 20px',
              fontSize: '1rem',
              backgroundColor: '#FF9800',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer',
              marginRight: '10px'
            }}
          >
              
          </button>
          
          <button 
            onClick={async () => {
              try {
                const health = await communicationService.getSystemHealth();
                alert(' : ' + JSON.stringify(health, null, 2));
              } catch (error) {
                alert('  : ' + error);
              }
            }}
            style={{
              padding: '10px 20px',
              fontSize: '1rem',
              backgroundColor: '#9C27B0',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer'
            }}
          >
              
          </button>
        </div>
        
        {isBlinking && (
          <div style={{ 
            fontSize: '2rem', 
            marginTop: '20px',
            fontWeight: 'bold',
            color: '#ff0000'
          }}>
               ! 
          </div>
        )}
      </div>
    </div>
  );
};

export default BusNotificationScreen; 